@import "toolkit/vars"; //Custom vars that override bootstrap vars

body {
	margin: 0;
	padding: 0;
}

.gm-wrapper {
    width: 100%;
    padding-bottom: 50%;
    position: relative;
}

.gm-flyout {
	position: absolute;
	width: 33.33%;
	height: 100%;
	background: rgba(1,1,1,.88);
	top: 0;
	left: 0;
}

.gm-flyout-content {
	color: white;
	padding: 50px $space;
}

.gm-flyout-close {
	width: 20px;
	height: 20px;
	top: 5px;
	right: 5px;
	position: absolute;
	background: white;
	text-indent: -9999px;
}

.gm-info-window {

	.title {
		display: block;
		font-size: 16px;
	}

	.content {

	}
}

#map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

